import React, { useState } from "react";
import SearchBar from "./components/SearchBar";
import styles from "../src/App.module.css";
import { motion } from "framer-motion";
import Cards from "./components/Cards";

function App() {
  const [city, setCity] = useState([]);
  const [backgroundImg, setBackgroundImg] = useState(
    "url(./imagenes/paisaje.jpg)"
  );

  const handleAddCity = (ciudad) => {
    setCity((prevCity) => {
      if (city.length === 3) {
        const newArr = [ciudad, ...prevCity];
        newArr.splice(3, 1);
        return newArr;
      } else return [ciudad, ...prevCity];
    });
  };

  const handleRemoveCity = (cityId) => {
    setCity((prevCity) => {
      return prevCity.filter((city) => {
        return cityId !== city.id;
      });
    });
  };

  async function loadInfo(city) {
    fetch(
      `${process.env.REACT_APP_URL}${city}&appid=${process.env.REACT_APP_KEY}`
    )
      .then((res) => res.json())
      .then((data) => {
        if (data.main !== undefined) {
          console.log(data);
          const ciudad = {
            min: Math.round(data.main.temp_min),
            max: Math.round(data.main.temp_max),
            temp: data.main.temp,
            desc: data.weather[0].main,
            img: data.weather[0].icon,
            id: data.id,
            name: data.name,
          };
          handleAddCity(ciudad);
        } else {
          alert("Ciudad no encontrada");
        }
      });
  }

  return (
    <motion.div className={styles.app} background-image={backgroundImg}>
      <SearchBar loadInfo={loadInfo} />
      <Cards dataCiudad={city} handleRemoveCity={handleRemoveCity} />
    </motion.div>
  );
}

export default App;
